.summary {
  border-top: 17.5vh #ffe0b2 solid;
  background: #202020;
  height: 100vh; }

.summary .content {
  color: #ffcc80;
  position: relative;
  text-align: center;
  top: 17.5vh;
  z-index: 10; }

@media (min-width: 768px) {
  .summary .content {
    width: 70%;
    margin-left: auto;
    margin-right: auto; } }

@media (min-width: 320px) and (max-width: 767px) {
  .summary .content {
    width: 90%;
    margin-left: auto;
    margin-right: auto; } }

.fullName {
